/**
 * 功能检测
 * want:
 * Detect.detect() => 所有检测
 * Detect.detect('') => 单个功能检测
 * Detect.detect(['', '', ...]) => 数组包含功能检测
 */
let Detect = {
	Browser: {

	},
	Ability: {
		fetch: window.fetch !== undefined, //self => Window 或 Worker
		Promise: window.Promise !== undefined,
		Map: typeof Map === 'function',
		['Object.assign']: Object.assign !== undefined,
		['Array.prototype.includes']: typeof Array.prototype.includes === 'function'
	},
	detect(arg){
		if(arg === undefined){
			this._log(JSON.stringify([this.Browser, this.Ability], null, '\t'))
			return Object.values(this.Browser).every(i => Boolean(i)) && Object.values(this.Ability).every(i => Boolean(i))
		}else if(typeof arg === 'string'){
			this.detectOne(arg)
		}else if(arg instanceof Array){
			for(let item of arg){
				this.detectOne(item)
			}
		}
	},
	detectOne(name){
		if (this.Browser.hasOwnProperty(name)) {
			this._log(JSON.stringify(this.Browser, [name], '\t'))
			return this.Browser[name]
		} else if (this.Ability.hasOwnProperty(name)) {
			this._log(JSON.stringify(this.Ability, [name], '\t'))
			return this.Ability[name]
		} else {
			return false
		}
	},
	_log: console.log,
	injectLog(fn){
		if (typeof fn === 'function') this._log = fn
	}
}

export default Detect