import Detect from './detect'
Detect.injectLog(showMsgPre)

function showMsg(msg) {
	let el = document.createElement('p');
	el.textContent = msg;
	document.querySelector('#msgs').appendChild(el)
}

function showMsgPre(msg) {
  let el = document.createElement('p')
  let pre = document.createElement('pre')
	pre.textContent = msg
	el.appendChild(pre)
	document.querySelector('#msgs').appendChild(el)
}

function browserSupportsAllFeatures() {
	return Detect.detect()
}

function loadScript(src, done) {
	let js = document.createElement('script');
	js.src = src;
	js.onload = done || (() => {});
	js.onerror = () => showMsg('Failed to load script ' + src);
	// document.body.insertBefore(js, document.querySelector('#js-index'));
	document.body.appendChild(js)
	// document.head.appendChild(js);
}

export {showMsg, showMsgPre, browserSupportsAllFeatures, loadScript}