// test function:
// 函数字面量: var add = function(){}
// 调用: this指向取决于调用模式 - 方法调用模式、函数调用模式、构造器调用模式、apply调用模式
// 扩充类型的功能
Function.prototype.method = function (name, func) {
  this.prototype[name] = func
  return this
}

// 递归: 汉诺塔、遍历树形结构(document.body)、
let walk_the_DOM = function walk(node, func) {
  func(node)
  node = node.firstChild
  while (node) {
    walk(node, func)
    node = node.nextSibling
  }
}

// 级联
// 柯里化
// 记忆
// recur: 递归函数；func: 定义递归规则；区分求值函数和取值函数
let memoizer = function (memo, func) {
  let recur = function (n) {
    let result = memo[n]
    if (typeof result !== 'number') {
      result = func(recur, n)
      memo[n] = result
    }
    return result
  }
  return recur
}

export {
  walk_the_DOM,
    memoizer
}