/**
 * 职业
 */
class Occupation{
	constructor(race){
		this.race = race;
	}
	getRace(){
		console.log('race is ', this.race)
	}
}

class Warrior extends Occupation{
	constructor(race, name){
		super(race);
		this.name = name;
		this.skill = new Set(['chongfeng', 'duanjian']);
	}
	getName(){
		console.log('name is ', this.name)
	}
	getSkill(){
		console.log('skill is ', [...this.skill])
	}
}

console.log(`Occupation's prototype is `,Occupation.prototype)
console.log('Warrior\'s prototype is ' , Warrior.prototype)
console.log('Warrior\'s __proto__ is Occupation' , Warrior.__proto__ === Occupation)
