// 设置斐波那契数列
let fibonacci = memoizer([0, 1], function (recur, n) {
  return recur(n - 1) + recur(n - 2)
})

let factorial = memoizer([0, 1], function (recur, n) {
  return n * recur(n - 1)
})

console.log(fibonacci(10))
console.log(factorial(0))